import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import Layout from "../../components/layout"
import Seo from "../../components/seo"
import "../../styles/main-archives.css"

export default function Home() {
  const description = `神奈川のIT相談ならホットソウルにお任せください！当社がデジタル化やIT活用でサポートさせていただいた実績をご紹介します。`
  const pageUrl = `https://www.hotsoul.jp/case-study/articles/`

  return (
    <Layout>
      <Seo title="サポート実績" description={description} pageUrl={pageUrl} />
      <div class="wrap">
        <section class="archives">
          <h2>サポート実績</h2>
          <div class="archives-inner">
            <div class="flex">
              <div class="flex-item">
                <figure>
                  <StaticImage
                    src="../../images/main-case-study/ph2.jpg"
                    alt="サポート事例"
                    placeholder="blurred"
                  />
                </figure>
                <p class="date">
                  2021.10.29
                </p>
                <p class="title">
                  【サポート事例】
                  <br/>
                  &nbsp;
                </p>
                <p class="txt">
                  どのようなサポートを必要とされていますか？
                  想定される事例をあげてみました。
                </p>
                <div class="archives-btn">
                  <Link to={`/case-study/`}>記事を読む</Link>
                </div>
              </div>
              <div class="flex-item">
                <figure>
                  <StaticImage
                    src="../../images/main-case-study/kanno-sharoshi/ph1.jpg"
                    alt="GO社会保険労務士法人様"
                    placeholder="blurred"
                  />
                </figure>
                <p class="date">
                  2021.7.21
                </p>
                <p class="title">
                  【IT環境 総合診断】
                  <br/>
                  GO社会保険労務士法人&nbsp;様
                </p>
                <p class="txt">
                  これで良いのか？とずっと不安でしたが、
                  総合的に診断して頂き、不安が消え自信が持てました。
                </p>
                <div class="archives-btn">
                  <Link to={`/case-study/kanno-sharoshi`}>記事を読む</Link>
                </div>
              </div>
              <div class="flex-item">
                <figure>
                  <StaticImage
                    src="../../images/main-case-study/yasumuro-setubi/ph1.jpg"
                    alt="有限会社安室設備様"
                    placeholder="blurred"
                  />
                </figure>
                <p class="date">
                  2022.11.25
                </p>
                <p class="title">
                  【業務用PCセットアップサポート】
                  <br/>
                  有限会社安室設備&nbsp;様
                </p>
                <p class="txt">
                  誰に相談すればいいのか分からず困っていましたが、
                  安心してお願いできるプロに一括でお任せできて良かったです。
                </p>
                <div class="archives-btn">
                  <Link to={`/case-study/yasumuro-setubi`}>記事を読む</Link>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  )
}